import React from 'react';
import styles from './UserStatsGrphs.module.css';
import { VictoryPie, VictoryChart, VictoryBar } from 'victory';

const graphStyle = {
    data: {
        fillOpacity: '0.9',
        stroke: "#fff",
        strokeWidth: 2,
        //fill: "b"
    },
    labels: {
        fontSize: 14,
        fill: "#333",
    }
}


const UserStatsGrphs = ({ data }) => {

    const [graph, setGraph] = React.useState([]);
    const [total, setTotal] = React.useState(0);



    React.useEffect(() => {
        const graphData = data.map((item) => {
            return {
                x: item.title,
                y: Number(item.acessos),
            }
        });

        function totalAccss() {
            const returned = data.map(({ acessos }) => Number(acessos));
            const returnedFinally = returned.reduce((acmld, item) => {
                return acmld + item;
            }, 0);
            setTotal(returnedFinally);
        }
        totalAccss();
        setGraph(graphData);

    }, [data]);

    return (
        <section className={`${styles.graph} animeLeft`}>

            <div className={`${styles.total} ${styles.graphItem}`}>
                <p>Acessos: {total}</p>
            </div>

            <div className={styles.graphItem}>
                <VictoryPie
                    data={graph}
                    innerRadius={50}
                    padding={{ top: 20, bottom: 20, left: 80, right: 80 }}
                    style={graphStyle}
                />
            </div>

            <div className={styles.graphItem}>
                <VictoryChart>
                    <VictoryBar alignment="start" data={graph}></VictoryBar>
                </VictoryChart>
            </div>
        </section>
    );
}


export default UserStatsGrphs;